.dp-swiper-container{margin:0 auto;position:relative;overflow:hidden;z-index:1}.dp-swiper-wrapper{position:relative;width:100%;height:100%;z-index:1;display:flex;transition-property:transform;box-sizing:content-box}.dp-swiper-slide{flex-shrink:0;width:100%;height:100%;display:flex}.dp-swiper-pagination{position:absolute;text-align:center;transition:300ms;transform:translate3d(0, 0, 0);z-index:10;bottom:10px;left:0;width:100%}.dp-swiper-pagination__bullet{width:8px;height:8px;display:inline-block;border-radius:100%;background:#000;opacity:0.2;margin:0 5px}.dp-swiper-pagination__bullet--active{opacity:1;background:#007aff}



